<template>
  <el-dialog
    class="popup-attachment"
    title="查看附件"
    :visible.sync="show"
    destroy-on-close
    @closed="onClose"
  >

    <el-table
      :data="fileList"
      size="mini"
      style="width: 100%">
      <el-table-column label="序号" type="index" align="center"></el-table-column>
      <el-table-column label="附件名称" prop="name" align="center"></el-table-column>
      <el-table-column label="附件格式" prop="format" align="center"></el-table-column>
      <el-table-column label="文件大小" prop="size" align="center">
        <template slot-scope="scope">{{ scope.row.size | fmtSize }}</template>
      </el-table-column>
      <el-table-column label="上传时间" prop="createTime" align="center">
        <template slot-scope="scope">{{ scope.row.createTime | fmtDateTime }}</template>
      </el-table-column>
      <el-table-column label="上传人" prop="creater" align="center"></el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-link icon="el-icon-download" @click="onDownLoad(scope.row.url)">下载</el-link>
        </template>
      </el-table-column>
    </el-table>

    <el-divider></el-divider>

    <div class="dialog-footer">
      <el-button size="small" plain @click="show = false">关 闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'popup-attachment',
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    visible: Boolean,
    fileList: Array
  },
  data () {
    return {
      show: false
    }
  },
  watch: {
    visible (val) {
      this.show = val
    }
  },
  filters: {
    fmtSize (val) {
      if (parseInt(val / 1024) === 0) {
        return val + 'B'
      }
      if (parseInt(val / 1024 / 1024) === 0) {
        return parseFloat(val / 1024).toFixed(2) + 'KB'
      }
      if (parseInt(val / 1024 / 1024 / 1024) === 0) {
        return parseFloat(val / 1024 / 1024).toFixed(2) + 'MB'
      }
    }
  },
  methods: {
    onClose () {
      this.$emit('update:visible', this.show)
    },

    // 下载附件
    onDownLoad (url) {
      // todo:下载文件
    }
  }
}
</script>

<style lang="scss" scoped>
.popup-attachment {
  .dialog-footer {
    text-align: center;

    .el-button {
      min-width: 100px;
    }
  }
}
</style>
